	module.exports = {"data":{"markdownRemark":{"html":"<p>With Hacker Reviews, you have the option to send comments on hacker behavior to HackerOne and the hacker after closing a report. The reviews also enable the reviewer to highlight a key behavior which can allow HackerOne to award or assist the hacker accordingly. </p>\n<p>When giving a review, you can: </p>\n<ul>\n<li>Choose whether to provide a positive or negative rating for the hacker.</li>\n<li>Select 1 of 4 preselected areas where the hacker excelled.</li>\n<li>Provide feedback to hackers and HackerOne about hacker behavior. </li>\n</ul>\n<h3>Enable Hacker Reviews</h3>\n<p>To enable hacker reviews:</p>\n<ol>\n<li>Go to Settings > Program > Hackbot</li>\n<li>Select <strong>Turn on</strong> for <em>Review reporter</em>.</li>\n</ol>\n<h3>How Reviews Work</h3>\n<p>To give a hacker review:</p>\n<ol>\n<li>Close the report and mark as resolved. </li>\n<li>\n<p>Hackbot will prompt you to give a review. You can choose from:</p>\n<ul>\n<li>Yes, it was great!</li>\n<li>Could have been better.</li>\n<li>No thanks!</li>\n</ul>\n</li>\n<li>\n<p>If you choose to give a:</p>\n<ul>\n<li>Positive review, your feedback form will look like:\n![</li>\n<li>Negative review, your feedback form will look like:\n![  </li>\n</ul>\n</li>\n</ol>\n<p>Regardless of whether the review is positive or negative, the hacker will receive an email with the contents of the review.\n![</p>\n<p>When the review is positive, the reviewer also has the option to leave a public endorsement on the hacker's profile page.\n![</p>\n<p>This serves as an opportunity to tell the world about just how awesome the hacker is.</p>","frontmatter":{"path":"/programs/hacker-reviews","title":"Hacker Reviews"}}},"pathContext":{}}

